.content-body {
    min-height: 100vh;
    padding: 54px 15px 60px;
    &.ppMain-content {
        padding: 0 0 70px
    }
    &.fixed-scroll {
        height: 100vh;
        overflow: hidden;
    }
    .subEtc-container {
        margin: 10px 0 20px;
        p {
            @include fontSize(12px,400);
            color: #666;
            line-height: 18px;
        }
    }
}


header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 54px;
    z-index: 10;
    .transparent-header {
        padding: 0 15px;
        height: 54px;
        @include flexCenter;
        position: relative;
        @keyframes showing {
            0% {
                opacity: 0;
            }
            100% {
                opacity: 1;
            }
        }
        @keyframes hideing {
            0% {
                opacity: 1;
            }
            100% {
                opacity: 0;
            }
        }
        &.active {
            background: #fff;
            transition: .3s;
            img {
                display: none;
                animation: hideing .3s ease-in;
                &.black-img {
                    display: block;
                    animation: showing .3s ease-in;
                }
            }
        }
        &.passive {
            img {
                display: block;
                animation: showing .3s ease-in;
                &.black-img {
                    display: none;
                    animation: hideing .3s ease-in;
                }
            }
        }
        img {
            &.black-img {
                display: none;
            }
        }
        button {
            @include btnNone;
        }
        .list-btn {
            position: relative;
            margin-right: 16px;
            span {
                position: absolute;
                top: -5px;
                right: -7px;
                @include fontSize(10px,700);
                color: #fff;
                background: $mainColor;
                padding: 0px 6px;
                border-radius: 99px;
            }
        }
        .btns-wrap {
            position: absolute;
            right: 15px;
            button {
                margin-right: 10px;
                &:last-of-type {
                    margin-right: 0;
                }
            }
        }
    }
    .default-header {
        padding: 0 15px;
        height: 54px;
        @include flexCenter;
        position: relative;
        background: #fff;
        .back-btn {
            @include btnNone;
            margin-right: 5px;
            padding-bottom: 2px;
        }
        .headerTitle {
            @include fontSize(20px,500);
            line-height: 29px;
        }
    }
}


footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 10;
    .tab-footer {
        .shadow-box {
            height: 15px;
            opacity: 0.15;
            background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #000000);
        }
        ul {
            width: 100%;
            @include flexCenter;
            li {
                width: 50%;
                text-align: center;
                padding: 10px 0 15px;
                background: #fff;
                img {
                    margin-bottom: 5px;
                }
                p {
                    line-height: 15px;
                    @include fontSize(10px,300);
                    color: #a2a2a2;
                }
                &.active {
                    p {
                        color: $mainColor;
                        @include fontSize(10px,500);
                    }
                }
            }
        }
    }
}