.content-body {
	min-height: 100vh;
	padding: 54px 15px 60px;
}

.content-body.ppMain-content {
	padding: 0 0 70px;
}

.content-body.fixed-scroll {
	height: 100vh;
	overflow: hidden;
}

.content-body .subEtc-container {
	margin: 10px 0 20px;
}

.content-body .subEtc-container p {
	font-size: 12px;
	font-weight: 400;
	color: #666;
	line-height: 18px;
}

header {
	position: fixed;
	top: 0;
	width: 100%;
	height: 54px;
	z-index: 10;
}

header .transparent-header {
	padding: 0 15px;
	height: 54px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	position: relative;
}

@keyframes showing {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@keyframes hideing {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}

header .transparent-header.active {
	background: #fff;
	transition: .3s;
}

header .transparent-header.active img {
	display: none;
	animation: hideing .3s ease-in;
}

header .transparent-header.active img.black-img {
	display: block;
	animation: showing .3s ease-in;
}

header .transparent-header.passive img {
	display: block;
	animation: showing .3s ease-in;
}

header .transparent-header.passive img.black-img {
	display: none;
	animation: hideing .3s ease-in;
}

header .transparent-header img.black-img {
	display: none;
}

header .transparent-header button {
	background: none;
	border: 0;
}

header .transparent-header .list-btn {
	position: relative;
	margin-right: 16px;
}

header .transparent-header .list-btn span {
	position: absolute;
	top: -5px;
	right: -7px;
	font-size: 10px;
	font-weight: 700;
	color: #fff;
	background: #ee5322;
	padding: 0px 6px;
	border-radius: 99px;
}

header .transparent-header .btns-wrap {
	position: absolute;
	right: 15px;
}

header .transparent-header .btns-wrap button {
	margin-right: 10px;
}

header .transparent-header .btns-wrap button:last-of-type {
	margin-right: 0;
}

header .default-header {
	padding: 0 15px;
	height: 54px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	position: relative;
	background: #fff;
}

header .default-header .back-btn {
	background: none;
	border: 0;
	margin-right: 5px;
	padding-bottom: 2px;
}

header .default-header .headerTitle {
	font-size: 20px;
	font-weight: 500;
	line-height: 29px;
}

footer {
	position: fixed;
	bottom: 0;
	width: 100%;
	z-index: 10;
}

footer .tab-footer .shadow-box {
	height: 15px;
	opacity: 0.15;
	background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0), #000000);
}

footer .tab-footer ul {
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}

footer .tab-footer ul li {
	width: 50%;
	text-align: center;
	padding: 10px 0 15px;
	background: #fff;
}

footer .tab-footer ul li img {
	margin-bottom: 5px;
}

footer .tab-footer ul li p {
	line-height: 15px;
	font-size: 10px;
	font-weight: 300;
	color: #a2a2a2;
}

footer .tab-footer ul li.active p {
	color: #ee5322;
	font-size: 10px;
	font-weight: 500;
}
